body {
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  height: 50px;
}



.button, .send-form {
  margin: 5px;
}

.modal-form {
  padding: 20px;
}

.title-form {
  margin-top: 20px;
}

.avatar {
  height: 50px;
  width: 50px;
  transition: 0.1s;
}

.avatar:hover {
  transform: rotate(10deg);
}

.main {
  padding: 60px 0;
}

.message-block {
  color: #343a40;
  position: relative;
}

.message-avatar {
  width: 100px;
  height: 100px;
}

.message-nickName {
  background-color: #f8f9fa;
  word-wrap: break-word;
  font-size: 24px;
}

.message-text {
  word-wrap: break-word;
  font-size: 20px;
  padding-bottom: 10px;
}

.message-date {
  font-size: 12px;
  padding-bottom: 10px;
}

.footer {
  height: 50px;
}
